rio ecológico. ")]),_c('p',[_vm._v("La materia orgánica es vital: regula los ciclos de nutrientes y mejora la estructura física, química y biológica del suelo. Un suelo con vida tiene capacidad de autorregulación y reduce la dependencia de insumos externos. Este principio transforma la visión del suelo como recurso explotable, reconociéndolo como memoria territorial y sujeto de cuidado colectivo.")])])]),_c('div',{staticClass:"div",attrs:{"titulo":"Mantener y conservar la biodiversidad"}},[_c('div',{staticClass:"ms-5"},[_c('p',[_vm._v("La diversidad de especies vegetales y animales, adaptadas a condiciones locales, contribuye a construir sistemas más estables, sanos y resilientes. Permite regular organismos considerados plagas, reducir arvenses, mejorar la disponibilidad de nutrientes y fomentar la polinización. ")]),_c('p',[_vm._v("Gracias a estas relaciones, el sistema funciona sin necesidad de productos químicos. La biodiversidad también asegura seguridad alimentaria, opciones de intercambio local y estrategias de cuidado territorial. No se trata solo de acumular especies, sino de entender sus funciones, asociaciones y vínculos con los saberes locales. Fomenta una visión de la finca como un espacio vivo e interrelacionado.")])])]),_c('div',{staticClass:"div",attrs:{"titulo":"Proteger y conservar la naturaleza"}},[_c('div',{staticClass:"ms-5"},[_c('p',[_vm._v("Este principio invita a respetar y cuidar los elementos naturales presentes en el territorio —nacederos, ríos, quebradas, humedales, bosques, flora y fauna silvestre— incluso si no se utilizan directamente en la producción. ")]),_c('p',[_vm._v("Estos espacios regulan el clima, atraen polinizadores, protegen el agua y sostienen la vida. Algunas especies que no ofrecen frutos ni sombra cumplen funciones invisibles pero esenciales. Además, conservar la naturaleza fortalece la capacidad de adaptación climática ante sequías, inundaciones y otros eventos extremos. Reconoce que la finca forma parte de un ecosistema