itan, trabajan y estudian el territorio. Este principio valora los saberes campesinos, indígenas, afrodescendientes y locales, acumulados y transmitidos por generaciones. ")]),_c('p',[_vm._v("También fomenta el diálogo horizontal entre estos saberes y los conocimientos científicos, técnicos o institucionales. No se busca imponer uno sobre otro, sino articularlos para enriquecer la toma de decisiones. Aprender juntos y reconocer la innovación desde el territorio son claves para una transición agroecológica profunda, autónoma y enraizada.")])])]),_c('div',{staticClass:"div",attrs:{"titulo":"Promover la organización social y la gobernanza participativa"}},[_c('div',{staticClass:"ms-5"},[_c('p',[_vm._v("La agroecología requiere que las decisiones sobre producción, uso del suelo y consumo sean tomadas colectivamente. Este principio promueve la creación de espacios de participación que incluyan a familias agricultoras, consumidores, mujeres, jóvenes, organizaciones comunitarias y entidades públicas. ")]),_c('p',[_vm._v("A través del diálogo, se construyen formas de gobernanza adaptadas al contexto local, fortaleciendo la democracia territorial y el respeto a los derechos humanos. Reconoce el papel estratégico de las mujeres y juventudes rurales en la transformación agroalimentaria, y defiende procesos organizativos horizontales, justos y sostenibles.")])])])]),_c('separador'),_vm._m(3),_c('p',{staticClass:"mb-5"},[_vm._v("A continuación, se presenta una síntesis práctica con acciones concretas que facilitan la aplicación de los principios agroecológicos tanto en la finca como en el territorio. La siguiente tabla ilustra cómo cada principio puede traducirse en prácticas cotidianas, útiles para fortalecer la transición agroecológica desde una perspectiva integral, comunitaria y territorial (FAO, 2024).")]),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-color-5 p-4 tarjeta",attrs:{"tipo":"b"}},[